import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			StatusEnumMap: misEnum.FoodRecordEnumMap,
			steps: [],
			color: '',
			info: {},
			showModal: false,
			failReason: ""
		}
	},
	onLoad(option) {
		global.$http.post('/dining/record/recordInfo', {
			params: {
				record_id: option.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
				//设置进度步骤
				if (this.info.status === 0) {
					this.color = 'text-cyan';
				} else if (this.info.status === -2) {
					this.color = 'text-orange';
				} else if (this.info.status === -1) {
					this.color = 'text-red';
				} else if (this.info.status === 1) {
					this.color = 'text-green';
				}
				var isFind = false;
				misEnum.FoodRecordEnumMap.forEach((value, key, map) => {
					var cuIcon = '';
					var color = '';
					if (!isFind || key == this.info.status) {
						color = this.color;
					}
					if (key == this.info.status) {
						isFind = true;
					}
					if (key === 0) {
						cuIcon = 'usefullfill';
					} else if (key === -2) {
						cuIcon = 'radioboxfill';
					} else if (key === -1) {
						cuIcon = 'roundclosefill';
					} else if (key === 1) {
						cuIcon = 'roundcheckfill';
					}
					this.steps.push({
						cuIcon: cuIcon,
						name: value,
						key: key,
						color: color
					})
				});

			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		verify(status, failReason) {
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/dining/record/sureRecord', {
				params: {
					record_id: this.info.id,
					status: status,
					fail_reason: failReason
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						title: "提交成功",
						icon: 'none'
					});
					this.info.status = status;
					if (status == 1) {
						this.steps[0].color = 'text-green';
						this.steps[1].color = 'text-green';
						this.steps[2].color = 'text-green';
						this.steps[3].color = 'text-green';
					} else {
						this.steps[0].color = 'text-red';
						this.steps[1].color = 'text-red';
						this.steps[2].color = 'text-red';
					}
					this.info.fail_reason = failReason;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		verifySuccess: function() {
			uni.showModal({
				title: '提示',
				content: '确定确认预定？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.cancel) return;
					this.verify(1, "");
				},
				fail: () => {},
				complete: () => {}
			});
		},
		verifyFail: function() {
			this.showModal = true;
		},
		hideModal: function() {
			this.showModal = false;
		},
		fillFailReason: function(e) {
			this.failReason = e.detail.value;
		},
		sureModal: function() {
			if (this.failReason.length <= 0) {
				uni.showToast({
					title: "请填写不通过原因",
					icon: 'none'
				});
				return;
			}
			this.verify(-1, this.failReason)
			this.showModal = false;
		}
	}
}
